import Vue from 'vue'
import Router from 'vue-router'
const  visitBaidu = r => require.ensure([], ()=>r(require('@/components/canguanbaidu/VisitBaidu.vue')))
const  visitEntry = r => require.ensure([], ()=>r(require('@/components/canguanbaidu/VisitEntry.vue')))
Vue.use(Router)
export default new Router({
  routes: [
    {
      path:'/',
      name:'visitEntry',
      component:visitEntry
    },
    {
      path:'/visit-info',
      name:'VisitBaidu',
      component:visitBaidu
    },
  ]
})



